@media (min-width:500px) {
    .app{
        display: flex;
        margin: auto;
        justify-content: center;
        align-self: center;
        margin: 50px 0;
    }
    .app div h3, .app div h1 {
        margin: 0 0 10px;
    }
    .app div h3{
        font-size: small;
    }
    .app-logos{
        margin-top: 20px;
        width: 100%;
        display: flex;
        justify-content: space-around;
    }
    .thenx-app-store{
        width: 50px;
        filter: invert();
        margin: 0 5px;
        opacity: .5;
        transition: all .3s ease;
    }
    .thenx-app-store:hover{
        opacity: 1;
        transition: all .3s ease;
    }
    .marquee img{
        margin: 0 10px; 
    }
}
@media only screen and (max-width: 499px){
    .app{
        display: flex;
        margin: auto;
        justify-content: center;
        align-self: center;
        margin: 50px 0;
        width: 100%;
    }
    .app div h3, .app div h1 {
        margin: 0 0 10px;
    }
    .app div h3{
        font-size: small;
    }
    .app-logos{
        margin-top: 20px;
        width: 100%;
        display: flex;
        justify-content: space-around;
    }
    .thenx-app-store{
        width: 50px;
        filter: invert();
        margin: 0 5px;
        opacity: .5;
        transition: all .3s ease;
    }
    .thenx-app-store:hover{
        opacity: 1;
        transition: all .3s ease;
    }
    .marquee img{
        margin: 0 10px; 
        width: 100px;
    }
}